.app__specialMenu {
 flex: 1;
 flex-direction: column;
 background-color: var(--color-black);
}

.app__specialMenu-title {
 margin-top: 2rem;
 text-align: center;
}
.app__specialMenu-menu {
 margin: 2rem 0;
 width: 100%;
 display: flex;
 justify-content: center;
 align-items: flex-start;
 flex-direction: row;
}
.app__specialMenu-menu_wine,
.app__specialMenu-cocktails {
 flex-direction: column;
 width: 100%;
 flex: 1;
}
.app__specialMenu-menu_img {
 width: 410px;
 margin: 0;
}
.app__specialMenu-menu_img img {
 height: auto;
 width: 100%;
}
.app__specialMenu-menu_heading {
 font-family: var(--font-base);
 font-weight: 600;
 font-size: 45px;
 text-align: center;
 letter-spacing: 0.04em;
 line-height: 58.8px;
 color: var(--color-white);
}

.app__specialMenu-menu_item {
 display: flex;
 flex-direction: column;
 margin: 2rem 0;
 width: 100%;
}
// Buttom
.app__specialMenu-button {
 margin-top: 15px;
}

@media screen and (max-width: 2000px) {
 .app__specialMenu-menu_img {
  width: 650px;
 }

 .app__specialMenu-menu_img img {
  height: 920px;
 }
}
@media screen and (max-width: 1150px) {
 .app__specialMenu-menu {
  flex-direction: column;
  align-items: center;
  width: 100%;
 }
 .app__specialMenu-menu_img {
  margin: 3rem 0;
 }
}

@media screen and (max-width: 650px) {
 .app__specialMenu-menu_img {
  width: 100%;
 }
 .app__specialMenu-menu_heading {
  font-size: 35px;
  line-height: 48.5px;
 }
}
