.app__gallery {
 display: flex;
 flex-direction: row;
 padding: 4rem 0 4rem 6rem;
 background-color: var(--color-black);
}

.app__gallery-content {
 flex: 1;
 display: flex;
 justify-content: center;
 align-items: flex-start;
 flex-direction: column;
 padding-right: 2rem;
 min-width: 500px;
 .app__gallery-btn {
  margin-top: 2rem;
 }
}

.app__gallery-images {
 flex: 1;
 flex-direction: row;
 display: flex;
 max-width: 50%;
 position: relative;
}
.app__gallery-images_container {
 display: flex;
 flex-direction: row;
 width: max-content;
 overflow-x: scroll;
 -ms-overflow-style: none;
 scrollbar-width: none;
 &::-webkit-scrollbar {
  display: none;
 }
}

.app__gallery-content p:first-child {
 margin-top: 2rem;
}

.app__gallery-images_card {
 position: relative;
 min-width: 301px;
 height: 447px;
 margin-right: 2rem;
 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  transition: 0.5s ease;
 }
}
.app__gallery-images_card:hover img {
 opacity: 0.35;
}
.app__gallery-images_card:hover .gallery__image-icon {
 opacity: 1;
}
.gallery__image-icon {
 position: absolute;
 font-size: 2rem;
 color: #fff;
 opacity: 0.5;
 transition: 0.05s ease;
 cursor: pointer;
}
.app__gallery-images_arrow {
 width: 100%;
 display: flex;
 justify-content: space-between;
 align-items: center;
 position: absolute;
 bottom: 30%;
}
.gallery__arrow-icon {
 color: var(--color-golden);
 font-size: 2rem;
 cursor: pointer;
 background-color: var(--color-black);
 border-radius: 5px;
 &:hover {
  color: var(--color-white);
 }
}

@media screen and (min-width: 2000px) {
 .app__gallery-content {
  min-width: 1000px;
  padding-right: 4rem;
 }
 .app__gallery-images_card {
  min-width: 400px;
  height: 550px;
 }
}

@media screen and (max-width: 1150px) {
 .app__gallery {
  flex-direction: column;
 }
 .app__gallery-images {
  max-width: 100%;
  margin: 5rem 0;
 }
}
@media screen and (max-width: 850px) {
 .app__gallery {
  padding: 4rem 0 4rem 4em;
 }
}
@media screen and (max-width: 650px) {
 .app__gallery {
  padding: 4rem 0 4rem 2em;
 }
 .app__gallery-content {
  min-width: 100%;
 }
 .app__gallery-images_card {
  min-width: 240;
  height: 320px;
 }
}
